body {
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

:root {
  --blue-color: #00acdc;
}

.logo__img {
  flex-shrink: 0;
  width: 40px;
  height: 40px;
  margin: auto 50px;
  display: block;
}

.nav {
  margin: 0 50px 0 0;
}

.nav__list {
  flex-direction: row;
  align-items: center;
  height: 60px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  white-space: nowrap;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  margin: auto 10px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: var(--blue-color);
}

.nav__item:last-child .nav__link {
  margin-right: 0;
}

.nav__item:first-child .nav__link {
  margin-left: 0;
}

.is-active {
  color: var(--blue-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

